import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';


class Page extends PureComponent {
    displayName = 'Page';
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>MessageBox </h1>
                <blockquote className='page-tip'>
                    <br/>
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo2></Demo2>
                    </Label>
                </Label>
                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo3></Demo3>
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>MessageBox</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'title', desc: '', type: 'string', default: ''},
                    {param: 'msg', desc: '', type: 'string', default: ''},
                    {param: 'type', desc: 'infoconfirm', type: 'string', default: 'info'},
                    {param: 'confirmText', desc: '', type: 'string', default: ' '},
                    {param: 'cancelText', desc: '', type: 'string', default: ' '},
                    {param: 'confirmTheme', desc: '', type: 'string', default: 'primary'},
                    {param: 'cancelTheme', desc: '', type: 'string', default: 'default'},
                    {param: 'confirmIcon', desc: '', type: 'string', default: 'check'},
                    {param: 'cancelIcon', desc: '', type: 'string', default: 'close'},
                    {param: 'footers', desc: 'footerPanel', type: 'object', default: ''},
                    {param: 'confirm', desc: '', type: 'function', default: ''},
                    {param: 'onHide', desc: '', type: 'function', default: ''},
                    {param: 'onShow', desc: '', type: 'function', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>show(msg, title)</i>
                        <span></span>
                        <ul>
                            <li>
                                msg {'String'} 
                            </li>
                            <li>
                                title {'String'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>hide()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>setData(data)</i>
                        <span></span>
                        <ul>
                            <li>
                                data {'any'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getData()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'any'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>showLoading()</i>
                        <span>loading</span>
                    </li>
                    <li>
                        <i>hideLoading()</i>
                        <span>loading</span>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



